import { Component, getAssetPath, h } from '@stencil/core';
import {Prop} from "@stencil/core";

@Component({
  tag: 'storm-sidenav-header',
  styleUrl: 'storm-sidenav-header.css',
  assetsDirs: ['assets'],
  shadow: false,
})
export class StormSidenavHeader {

  @Prop() name : string;
  @Prop() image : string;
  @Prop() basePath : string = "/";

  render() {
    return (
      <div class="sidenav-header">
        <i class="fas fa-times p-3 cursor-pointer text-white opacity-5 position-absolute end-0 top-0 d-none d-xl-none" aria-hidden="true" id="iconSidenav"></i>
        <a class="navbar-brand m-0" href={`${this.basePath}`} >
          <img src={getAssetPath(`../../img/${this.image}`/*`./assets/${this.image}`*/)} class="navbar-brand-img h-100" alt="main_logo"/>
          <span class="ms-1 font-weight-bold text-white">{this.name}</span>
        </a>
      </div>
    );
  }
}
