import { Component, Element, Event, EventEmitter, h, Method, Prop, State } from '@stencil/core';

@Component({
  tag: 'storm-sidenav-collapse',
  styleUrl: 'storm-sidenav-collapse.css',
  shadow: false,
})
export class StormSidenavCollapse {
  @Element() host: HTMLElement;
  @Prop() dataSet: IFunction[];
  @Prop({ mutable: true }) value: string;
  @Event() change: EventEmitter<IFunction>;
  @State() dictionary: Record<string, IFunction> = {};
  // private functionRefs: HTMLElement[]

  // componentWillRender() {
  //   this.functionRefs = []
  // }

  getClass(fun: IFunction): string {
    let isActive = fun.isActive;

    if (this.value == fun.id) isActive = true;

    return `nav-link text-white ${isActive ? 'active bg-gradient-primary' : ''}`;
  }

  onClick = event => {
    event.preventDefault();

    // this.host.querySelectorAll(".active").forEach(e => e.classList.remove('active'));
    // this.host.querySelectorAll(".bg-gradient-primary").forEach(e => e.classList.remove('bg-gradient-primary'));

    // event.target.classList.add('active');
    // event.target.classList.add('bg-gradient-primary');

    this.removeActiveAll();
    this.addActive(event.target);

    this.value = event.target.getAttribute('data-id');
    this.change.emit(this.dictionary[this.value]);
  };

  removeActiveAll() {
    // this.functionRefs.forEach(elem => elem.classList.remove('active','bg-gradient-primary'))
    this.host.querySelectorAll('.active').forEach(elem => elem.classList.remove('active', 'bg-gradient-primary'));
  }

  addActive(e: HTMLElement) {
    e.classList.add('active', 'bg-gradient-primary');
  }

  @Method()
  async setLabel(fun: IFunction): Promise<void> {
    this.dictionary[fun.id] = fun;

    const element = this.host.querySelector(`a[data-id="${fun.id}"] > span`);
    element.textContent = fun.name;
  }

  render() {
    // this.functionRefs = []
    return (
      <ul class="navbar-nav">
        {this.dataSet?.map((fun: IFunction) => {
          this.dictionary[fun.id] = fun;
          return (
            <ul class="nav ">
              <li class="nav-item mt-3">
                <h6 class="ps-4  ms-2 text-uppercase text-xs font-weight-bolder text-white">{fun.name}</h6>
              </li>
              {fun.children?.map((sub: IFunction) => {
                this.dictionary[sub.id] = sub;
                return (
                  <li class="nav-item">
                    <a class={this.getClass(sub)} href={sub.href} onClick={e => this.onClick(e)} data-id={sub.id} /*ref={e => this.functionRefs.push(e)}*/>
                      {/* <div class="text-white text-center me-2 d-flex align-items-center justify-content-center"> */}
                      <i class="material-icons opacity-10">{sub.icon}</i>
                      {/* </div> */}
                      <span class="nav-link-text ms-2 ps-1">{sub.name}</span>
                    </a>
                  </li>
                );
              })}
            </ul>
          );
        })}
      </ul>
    );
  }
}

export interface IFunction {
  id: string;
  name: string;
  children: IFunction[];
  href: string;
  icon: string;
  isActive: boolean;
}
